import React, { useState } from "react";
import AddIcon from "@material-ui/icons/Add";

function CreateArea(props){
    const [typing , setTyping] = useState(false);
    const [note , setNote] = useState({
        title: String,
        content: String,
    });

    function onNoteChange(event){
        const name = event.target.name;
        const value = event.target.value;

        setNote(previous => {
            return {
                ...previous,
                [name]: value,
            }
        });
    }

    function onAddNote(event){
        props.onAddNote(note);
        setNote({
            title: "",
            content: "",
        });
        event.preventDefault();
    }

    function typingHandler(){
        setTyping(true);
    }

    return (
        <form className="create-note" onSubmit={onAddNote}>
            {typing && <input name="title" type="text" placeholder="title" onChange={onNoteChange} value={note.title}/>}
            <textarea name="content" rows={typing ? "4" : "1"} placeholder="take a note..." onChange={onNoteChange} value={note.content} onKeyPress={typingHandler}/>
            {typing && <button type="submit"><AddIcon/></button>}
        </form>
    )
}

export default CreateArea;